
.sidebar-title {
  font-size: 26px;
  padding-top: 100px;
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1001;
  pointer-events: none;
  visibility: hidden;
  opacity: 0;




  .list-block {
    ul {
      padding-left: 25px;
      margin-top: 20px;
      height: 230px;
    }
  }
  .widget {
    margin: 20px 0;
    input[type="checkbox"] {
      display: none;
      &:checked ~ div {
        max-height: 1000px;
        opacity: 1;
        visibility: visible;
      }
      &:checked ~ label {
        i {
          transform: rotate(180deg);
        }
      }
      &:where(.accord > div, range-track) {
        padding-top: 20px;
      }

    }


    .accord {
      padding-left: 15px;
      max-height: 0;
      opacity: 0;
      visibility: hidden;
      overflow: hidden;
      transition: all .3s ease;
    }
  }

  label, .price-range {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    span {
      font-weight: 600;
    }
    i {
      font-size: 26px;
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center ;
      transition: var(--transition-transform);
    }
  }
}
.sidebar > .wrap {
  position: relative;
  max-width: calc(100% - 40px);
  width: 360px;
  height: 100%;
  background-color: var(--white-color);
  pointer-events: auto;
  transform: translateX(0);

}
.sidebar .close-trigger {
  position: absolute;
  top: 0;
  right: -40px;
  width: 40px;
  height: 40px;
  color: var(--white-color);
  display: flex;
  align-items: center;
  justify-content: center;
}

.sidebar.active {
  visibility: visible;
  opacity: 1;

}

.sidebar-content {
  height: 100%;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
}

input[type="range"] {
  -webkit-appearance: none;
  width: 100%;
}
input[type="range"]:focus {
    outline: none;
}

input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  background-color: var(--light-color);
  box-shadow: none;
  border: 0;
}

input[type="range"]::-webkit-slider-thumb {
  z-index: 2;
  position: relative;
  height: 18px;
  width: 14px;
  background-color: var(--grey-color);
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -7px;
}

.sidebar .price-range {
  font-size: 18px;
}

