@media (min-width:992px) {
  .sidebar-title, .sidebar-content {
    padding: 0;
  }
  .sidebar ,
  .sidebar> .wrap {
    all: unset;
  }
  .page-category {
    .content {
      display: grid;
      grid-template-columns: 1fr 4fr;
      gap: 60px;


    }

  }
}



.category-content {
  .sort-list {
    :where(.wrap > ul),
      .list-inline>ul {
        position: relative;

        display: flex;
        align-items: center;
        gap: 10px;

      }

    .wrap {
      position: relative;

      ul {
        position: absolute;
        text-align: left;
        top: 50px;
        left: 0;
        right: 0;
        background-color: var(--white-color);
        box-shadow: var(--shadow);
        z-index: 10;
        display: none;
        animation: var(--fade);

      }
      ul.show {
        display: block;
      }

    }
  }
  .dotgrid {
    .wrapper {
      display: grid;
      --grid-col: 221px;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 15px;
      @media (max-width:972px) {
        grid-template-columns: 1fr 1fr;
      }
      @media (max-width:768px) {
        grid-template-columns: 1fr;
      }

    }
  }
  .sort-list ul {
    width: 180px;

    li {
      padding: 5px 5px;

    }
    li.active {
      background-color: var(--dark-color);
      color: var(--white-color);

    }







  }
  .right {
    .list-inline {

      a {

        display: flex;
        padding: 7px;
        background-color: var(--flashwhite-color);
        margin-left: 10px;
        border-radius: 3px;
      }

    }
  }

  .list-inline {
    ul {
      display: flex;

    }
  }



  .button {
    margin: 60px 0 100px;
    text-align: center;
  }
  :where(.sorter, .right) {

    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .sorter {

    margin: 0 0 30px;

  }

  .opt-trigger {

    gap: 5px;
    margin-right: 15px ;
    line-height: 1.4;
    cursor: pointer;
    display: flex;
    align-items: center;

    span {
      font-size: 14px;
      font-weight: 600;
    }
    i {
      font-size: 24px;
    }


  }
  @media (max-width:768px) {
    .left {
      display: none;
    }

  }

}

@media (max-width: 992px) {
  .sorter > a {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 44px;
    width: 44px;
    background-color: var(--dark-color);
    color: var(--white-color);
    border-radius: 50%;
    position: fixed;
    top: calc(50% - 20px);
    left: 0;
    z-index: 10;
    opacity: .5;
    transition: opasity .3s, var(--transition-transform);
    &:hover {
      transform: scale(1.2);
      opacity: 1;
    }
  }
}

